.owl-carousel {
    &.full-width {
        .owl-nav {
            position: absolute;
            top: 45%;
            width: 100%;
            margin-top: 0;
            .owl-prev,
            .owl-next {
                background: transparent;
                color: $white;
                i {
                    &:before {
                        width: 60px;
                        height: 60px;
                        background: rgba($black, .5);
                        border-radius: 100%;
                        font-size: 1.875rem;
                        font-weight: bold;
                        line-height: 2;
                        @media ( max-width: 765px ) {
                            width: 30px;
                            height: 30px;
                            font-size: 1rem;
                        }
                    }
                }
            }
            .owl-prev {
                float: left;
            }
            .owl-next {
                float: right;
            }
        }
        .owl-dots {
            position: absolute;
            bottom: 20%;
            width: 100%;
            margin-top: 0;
            .owl-dot span {
                width: 14px;
                height: 14px;
                @media ( max-width: 765px ) {
                    width: 10px;
                    height: 10px;
                    font-size: 1rem;
                }
            }
        }
    }
}